            <div class="box_5 flex-row justify-between">
              <span class="text_3">专递课表</span>
              <img
                class="label_1"
                referrerpolicy="no-referrer"
                src="./assets/img/SketchPngd332e8d05853aa765ea961aa2db9964f1ee6748a1aeabbfa8174745ac8fa6432.png"
                @click="handleRefresh"
              />
            </div>
            <div
              class="box_6 flex-col"
