                <span class="text_7">{{ item.classesName }}</span>
                <span class="text_8">{{ item.courseName }}</span>
              </div>
            </div>
          </div>
          <div class="box_7 flex-col justify-end">
            <div class="block_3 flex-col" @click="()=>(this.meetingDialog = true)">
              <span class="text_19">网络教研</span>
              <img
