        <div class="popup__dialog__content flex-row">
          <div>会议方式:</div>
          <div
            class="popup__dialog__content__item bg1"
            @click="handleEntry(0)"
          ></div>
          <div
            class="popup__dialog__content__item bg2"
            @click="handleEntry(1)"
          ></div>
          <div
            class="popup__dialog__content__item bg3"
            @click="handleEntry(2)"
          ></div>
        </div>
        <div class="popup__dialog__join">
          <div class="popup__dialog__join__title">加入教研:</div>
          <div class="popup__dialog__join__list">

              <div class="popup__dialog__join__list__item" 
              v-for="item in liveRooms" :key="item.id"
              @click="tempInteractive(item)">{{item.name}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
  </div>

</template>
<script>
import {
  getProDetail,
  getEnterRoomUrl,
  getClassRoomDetail,
  getEnterMeetingUrl,
  getliveRoom
} from "../../api/Home";
export default {
  data() {
    return {
      meetingDialog: false,
      hdmi1:false,
      hdmi2:false,
      constants: {},
      terminalName: "",
      roomId: "",
      mode: "",
      talkCloudId: "",
      list: [],
      formatDate: "",
      currentHour: "",
      currentMin: "",
      network: "",
      liveRooms:[],
      flag:false
    };
  },
  methods: {
    handleEntryRoom(item) {
      const { id } = item;
      getEnterRoomUrl(id)
        .then((res) => {
          window.location.href = res.data.enterRoomUrl;
        })
        .catch((err) => {
          alert(err.toString());
        });
    },
    initTime() {
      let date = new Date();
      this.currentHour = date.getHours();
      this.currentMin = date.getMinutes();
      this.formatDate = `${date.getFullYear()}年${
        date.getMonth() + 1
      }月${date.getDate()}日`;
      setInterval(() => {
        let date = new Date();
        this.currentMin = date.getMinutes();
        this.$forceUpdate();
      }, 6000);
    },
    handleRefresh() {
      window.location.reload();
    },
    // 获取hdmi
    getHdmi(){
      const hdmi1 =  window.localStorage.getItem('hdmi1');
      const hdmi2 =  window.localStorage.getItem('hdmi2');

      if(hdmi1 == '1'){
        this.hdmi1 = true;
      }else{
        this.hdmi1 = false;
      }
      if(hdmi2 == '1'){
        this.hdmi2 = true;
      }else{
        this.hdmi2 = false;
      }
    },
    // 进入会议
    handleEntry(type) {
      let temp = {};
      temp.talkCloudId=this.talkCloudId 
      switch (type) {
        case 0:
          this.tempInteractive(temp);
          break;
        case 1:
          this.toLark();
          break;
        case 2:
          this.toWemeet();
          break;
        default:
          console.log("进入互动失败");
          return;
      }
    },
    tempInteractive(item) {
      let temp = {
        talkCloudId: item.talkCloudId,
        userType: 0,
        joinClassRoomName: this.terminalName,
      };
      getEnterMeetingUrl(temp).then((res) => {
        window.location.href = res.data.enterRoomUrl;
        if (res.data.result == 4007) {
          this.$message({
            message: "教室不存在",
            type: "warning",
          });
        }
      });
    },
    // 飞书
    toLark() {
      window.location.href = "feishu://";
    },
    // 腾讯会议
    toWemeet() {
      window.location.href = "wemeet://";
    },
    // 关机
   
  },
  computed: {
    nowTime() {
      let Hour =
        this.currentHour >= 10 ? `${this.currentHour}` : `0${this.currentHour}`;
      let Min =
        this.currentMin >= 10 ? `${this.currentMin}` : `0${this.currentMin}`;
      return `${Hour}:${Min}`;
    },
  },
  async created() {
    // this.terminalName = window.localStorage.getItem("terminalName");
    this.roomId = window.localStorage.getItem("roomId");
    this.mode = window.localStorage.getItem("mode");
    this.talkCloudId = window.localStorage.getItem("talkCloudId");
    this.network = window.localStorage.getItem("network") == 1 ? true : false;

    await getClassRoomDetail(this.roomId).then((res) => {
      this.terminalName = res?.data?.data?.name;
    });
    await getProDetail(this.roomId).then((res) => {
      this.list = res.data.data;
    });
    await getliveRoom(this.roomId).then(res=>{
      this.liveRooms = res.data.data;
    });
    setInterval(this.getHdmi,5000);
    this.initTime();
    
  },
};
</script>
